import { useRef, useEffect } from "react";

const getPixelRatio = (context) => {
  var backingStore =
    context.backingStorePixelRatio ||
    context.webkitBackingStorePixelRatio ||
    context.mozBackingStorePixelRatio ||
    context.msBackingStorePixelRatio ||
    context.oBackingStorePixelRatio ||
    context.backingStorePixelRatio ||
    1;

  return (window.devicePixelRatio || 1) / backingStore;
};

function IntersectionCanvas({cars}) {
  const canvasRef = useRef();

  useEffect(() => {
    let canvas = canvasRef.current;
    let context = canvas.getContext("2d");

    let ratio = getPixelRatio(context);
    let width = getComputedStyle(canvas).getPropertyValue("width").slice(0, -2);
    let height = getComputedStyle(canvas).getPropertyValue("height").slice(0, -2);

    canvas.width = width * ratio;
    canvas.height = height * ratio;

    let requestId, i = 0;
    let indexes = cars.map((x) => 0);
    
    const render = () => {
        context.clearRect(0, 0, canvas.width, canvas.height);

        const lowerBound = canvas.width * 0.35;
        const upperBound = canvas.width * 0.65;

        context.fillStyle = 'lightgray';
        context.fillRect(0, 0, lowerBound, lowerBound);
        context.fillRect(upperBound, 0, lowerBound, lowerBound);
        context.fillRect(0, upperBound, lowerBound, lowerBound);
        context.fillRect(upperBound, upperBound, lowerBound, lowerBound);

        const stoplineWidth = 0.5 * (upperBound - lowerBound);
        const stoplineHeight = 10;
        context.fillStyle = 'lightgray';
        context.fillRect(lowerBound, lowerBound - stoplineHeight, stoplineWidth,stoplineHeight);
        context.fillRect(upperBound - stoplineWidth, upperBound, stoplineWidth,stoplineHeight);
        context.fillRect(upperBound, lowerBound, stoplineHeight,stoplineWidth);
        context.fillRect(lowerBound - stoplineHeight, upperBound - stoplineWidth, stoplineHeight,stoplineWidth);

        for (let j = 0; j < cars.length; j++){
            context.beginPath();
            const car = cars[j];
            let index = indexes[j];
            if (i >= car['times'][index+1] || i === 0){
                index = (index + 1) % (car['intervals'].length);
                indexes[j] = index;
            }

            const lambda = (i - car['times'][index])/car['intervals'][index];
            const x = (1-lambda)*car['x'][index]*canvas.width+ (lambda)*car['x'][index+1]*canvas.width;
            const y = (1-lambda)*car['y'][index]*canvas.height+ (lambda)*car['y'][index+1]*canvas.height;

            context.fillStyle = '#3CA542';
            context.arc(
                x, 
                y, 
                canvas.width*0.03, 
                0, 
                2 * Math.PI
            );
            context.fill();
            context.strokeStyle = '#000000';
            context.stroke();
        }
        
        i += 0.005; // 1/60
        // console.log(i);
        if (i > 24){ // 24
            i = 0;
            // console.log("reset")
        }
        requestId = requestAnimationFrame(render);
    };

    render();

    return () => {
        cancelAnimationFrame(requestId);
    };
  });

  return <canvas ref={canvasRef} className="intersection-canvas"></canvas>;
}

export default IntersectionCanvas;
