import PropTypes from "prop-types";

import H4 from "../basicComponents/h4";
import H3 from "../basicComponents/h3";

import strings from "../../res/strings";

function AboutProfil({ name, picturePath, jobTitle, children, hasLink, linkPath }) {
  /**
   * a single tile, describing one person of the team.
   *
   * @param {string} name             name of the person
   * @param {string} picturePath      path to the picture file of the person
   * @param {string} jobTitle         job title of the person
   * @param {string} description      short description of bio of the person
   * @param {object} children         children elements
   * @param {boolean} hasLink         profil has resume linked
   * @param {string} linkPath         path to resume
   */
  return (
    <div className="col p-5">
      <img src={picturePath} className="img-thumbnail roundedImage" alt="profil"/>
      <H3 className="pt-3">{name}</H3>
      <H4 className="jobTitle">{jobTitle}</H4>
      {children}
      {hasLink && <a href={linkPath} >{strings.cv}</a>}
    </div>
  );
}

AboutProfil.propTypes = {
  name: PropTypes.string,
  picturePath: PropTypes.string,
  jobTitle: PropTypes.string,
  children: PropTypes.object,
  hasLink: PropTypes.bool,
  linkPath: PropTypes.string
};

AboutProfil.defaultProps = {
  name: "Matthias Hamacher",
  picturePath: "./Matthias_Hamacher_Picture.jpg",
  jobTitle: "Project Lead, Machine Learning and Front-End Developer",
  hasLink: false,
};

// Project Lead, Machine Learning and Front-End Developer
// "./Matthias_Hamacher_Picture.jpg"

export default AboutProfil;
