import React from "react";
import { Form, Button } from "react-bootstrap";
import { init } from "emailjs-com";
import emailjs from "emailjs-com";

import ContactLine from "../components/contactComponents/contactLine";
import H1 from "../components/basicComponents/h1";

import emailKey from "../components/emailkey";
import strings from "../res/strings";

init("user_JYqjNIUDvP10esOt6DM90");

const handleSubmit = (e) => {
  e.preventDefault(); // Prevents default refresh by the browser
  let contactLines = e.target.children;
  let temp = {
    email: contactLines[1].children[0].value,
    to_name: contactLines[0].children[0].value,
    message: contactLines[3].children[0].value,
    subject: contactLines[2].children[0].value,
  };
  console.log(contactLines);
  emailjs
    .send("service_zq3184g", emailKey.TEMPLATE_ID, temp, emailKey.USER_ID)
    .then(
      (result) => {
        alert(strings.sent, result.text);
      },
      (error) => {
        alert(strings.sentError, error.text);
      }
    );
};

function Contact() {
  return (
    <div className="pb-3 page lead">
      <div className="head">
        <H1 className="mt-3">{strings.contact}</H1>
      </div>

      <div className="p-5">
        <Form onSubmit={handleSubmit} className="pageL">
          <ContactLine
            controlId="to_name"
            placeholder={strings.namePlaceholder}
            title={strings.nameTitle}
            mutedText=""
          ></ContactLine>
          <ContactLine
            controlId="email"
            placeholder={strings.mailPlaceholder}
            title={strings.mailTitle}
            mutedText={strings.mailSecurity}
          ></ContactLine>
          <ContactLine
            controlId="formBasicSubject"
            placeholder={strings.subjectPlaceholder}
            title={strings.subjectTitle}
            mutedText=""
          ></ContactLine>
          <ContactLine
            controlId="message"
            placeholder={strings.textPlaceholder}
            title={strings.textTitle}
            mutedText=""
            rows={7}
          ></ContactLine>
          <Button
            variant="light"
            type="submit"
            size="lg"
            style={{ marginTop: "20px" }}
            block
          >
            {strings.submit}
          </Button>
        </Form>
      </div>
    </div>
  );
}

export default Contact;
