import PropTypes from 'prop-types';
import {
    Form
  } from "react-bootstrap";

/**
 * 
 * @param placeholder - 
 * @param contactId - not sure what this does
 * @param mutedText - hint text below the input
 * @param ref - react useRef reference
 * @param onSubmit - function called on submit
 * @param rows - number of rows of the input field
 * @returns 
 */
function ContactLine({placeholder, controlId, mutedText, onSubmit, rows, ref}){

    return (
        <Form.Group controlId={controlId} style={{paddingTop: "20px"}}>
          <Form.Control type={controlId === 'email'? 'email': 'text'} placeholder={placeholder} ref={ref} onSubmit={onSubmit} 
          as={rows > 1 ? "textarea": "input"} rows={rows} name={controlId} required/>
          <Form.Text className="text-muted" style={{color: "gray"}}>
            {mutedText}
          </Form.Text>
        </Form.Group>
    );
}

ContactLine.propTypes = {
    placeholder: PropTypes.string,
    controlId: PropTypes.string,
    mutedText: PropTypes.string,
    children: PropTypes.elementType,
    rows: PropTypes.number,
    onSubmit: PropTypes.func,
}

ContactLine.defaultProps = {
    type: "email",
    placeholder: "placeholder",
    controlId: "formBasicEmail",
    mutedText: "Text",
    rows: 1,
}

export default ContactLine